import { CST } from '../CST';

export class MenuScene extends Phaser.Scene {
    constructor() {
        super({
            key: CST.SCENES.MENU
        });
    }
    preload() {}
    create() {
        this.sound.play('MENU_MUSIC', {
            loop: true
        });

        this.bg = this.add.image(0, 0, 'MENU_BG').setOrigin(0);
        this.bg.alpha = 0.2;
        this.bg.displayWidth = this.game.renderer.width;
        this.bg.displayHeight = this.game.renderer.height;

        let opacityTimer = setInterval(() => {
            if (this.bg.alpha == 1) clearInterval(opacityTimer);
            // console.log(`Menu opacity: ${this.bg.alpha}`);
            this.bg.alpha = this.bg.alpha + 0.01
        }, 300)

        let loginButton = this.add.image(this.game.renderer.width / 2, this.game.renderer.height - 60, 'LOGIN_BTN').setScale(2);
        loginButton.setInteractive();
        loginButton.on('pointerdown', () => {
            this.scene.start(CST.SCENES.GAME);
            this.sound.stopAll();
        });

        let registerButton = this.add.image(this.game.renderer.width / 2, this.game.renderer.height - 30, 'REGISTER_BTN').setScale(2);
        registerButton.setInteractive();
        registerButton.on('pointerdown', () => {
            this.scene.start(CST.SCENES.GAME);
            this.sound.stopAll();

        }); 
    }
}